body{
    --tableFontSize:12px;
    --headTableFontSize: 16px
}
.table_page{
    text-align: center;
}
.table_page h2{
    padding: 1%;
}

.table_group{
    padding: 1%;
    text-align: center;
    padding-bottom: 5%;
}
.table_row{
   text-align: center;
   font-weight: 900;
}

.table {
    width: 100%;
    margin: 0px;
    table-layout: fixed;
    font-size: var(--tableFontSize);
    border-spacing: 0px;
    
}
.cell_text{
    font-size: var(--tableFontSize);
    padding: 1%;
    padding-left: 0.5%;
    padding-right: 0.5%;
    width: fit-content;
}
.cell_text.head{
    font-size: var(--headTableFontSize)
}
.cell_text > a{
    text-decoration: none;
}
.cell_text > a:visited{
    color: inherit;
}
.table_container{
    margin: 0px;
    margin-bottom: 2%;
    padding: 3%;
}
.table_footer{
    width: 100%;
    margin: 0%;
    position: absolute;
}
.table_footer td{
    width: 73%;
    margin: 0%;
    position: absolute;
}

.page_container{
    display: flex;
}
.content{
    flex: 75%;
    width: 100%;
}
.settings{
    flex:20%;
    width: 50%;
}