.navbar{
    background-color:#cd2464;
    height: 10%;
    margin-top: 0px;
    font-size: x-large;
    text-align: left;
    width: 100%;
    color: aliceblue;
    
}
.navbar ul{
    display: flex;
    width: 60%;
}
.navbar ul li{
    list-style: none;
    padding: 2%;
    display: flex;
}
.navbar ul li a{
    text-decoration: none;
    color: aliceblue;
    padding-top: 12%;
    padding-bottom: 12%;
    vertical-align:middle;
    display: flex;
}
.navbar svg{
    margin: 3%;
    width: 100%;
}
.logo{
    width: 50px;
    height: 50px;
    border-radius: 50%;
}